import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { hydrate, render } from 'react-dom';
import routes from 'Routes.core';
import App from 'App.core';
import * as OfflinePluginRuntime from 'offline-plugin/runtime';

// Grab the state from a global variable injected into the server-generated HTML

// Allow the passed state to be garbage-collected
const MainApp = () => (
  <BrowserRouter>
    <App>
      <Switch>
        {routes.map((x, i) => (
          // eslint-disable-next-line react/no-array-index-key
          <Route {...x} key={i} />
        ))}
      </Switch>
    </App>
  </BrowserRouter>
);

const createRenderedRoute = () =>
  hydrate(<MainApp />, document.getElementById('root'));

const createNewApp = () => render(<MainApp />, document.getElementById('root'));

if (routes.find(x => x.path === location.pathname)) {
  createNewApp();
} else {
  createRenderedRoute();
}

if (!__DEV__ && OFFLINE) {
  OfflinePluginRuntime.install();
}

if (module.hot) {
  module.hot.accept();
}
